.nav {
    grid-area: n;
    padding: 10px;
}

.item a {
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-weight: bolder;
}

.item a.activeLink {
    color: #bbbb2e;
}

.friends {
    padding-top: 40px;
}

.friendsItem img {
    width: 35px;
    height: 35px;
    border-radius: 30px;
}

.friendsItem {
    padding-top: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 15px;
}

.friend {
    display: flex;
    flex-direction: column;
    align-items: center;
}